import React, { useEffect, useState } from "react";
import { useAuthState } from "react-firebase-hooks/auth";
import { Link, useNavigate } from "react-router-dom";
import "./Dashboard.css";
import { auth, db, logout } from "./firebase";
import { query, collection, getDocs, where, doc, addDoc, setDoc, updateDoc, getDoc, onSnapshot } from "firebase/firestore";
import Header from "./header";
import {
    MDBBtn,
    MDBModal,
    MDBModalDialog,
    MDBModalContent,
    MDBModalHeader,
    MDBModalTitle,
    MDBModalBody,
    MDBModalFooter,
    MDBSpinner
} from 'mdb-react-ui-kit';
import EditIcon from '@mui/icons-material/Edit';

function ProfileEdit() {    
    const [user, loading, error] = useAuthState(auth);
    const [name, setName] = useState('');
    const [telefone, setTelefone] = useState('');
    const [cidade, setCidade] = useState('')
    const [descricao, setDescricao] = useState('')   
    const [whatsapp, setWhatsapp] = useState('')  
    const navigate = useNavigate();
    const [nameModal, setNameModal] = useState(false);
    const toggleShowName = () => setNameModal(!nameModal);
    const [cidadeModal, setCidadeModal] = useState(false);
    const toggleShowCidade = () => setCidadeModal(!cidadeModal);
    const [descricaoModal, setDescricaoModal] = useState(false);
    const toggleShowDescricao = () => setDescricaoModal(!descricaoModal);
    const [whatsappModal, setWhatsappModal] = useState(false);
    const toggleShowWhatsapp = () => setWhatsappModal(!whatsappModal);
    const [telefoneModal, setTelefoneModal] = useState(false);
    const toggleShowTelefone = () => setTelefoneModal(!telefoneModal);
   
    async function fetchUserName() {
        try {
            const q = query(collection(db, "users"), where("uid", "==", user?.uid));
            const doc = await getDocs(q);
            const data = doc.docs[0].data();
            setName(data.name);
            setCidade(data.cidade);
            setDescricao(data.descricao)
            setWhatsapp(data.whatsapp)
            setTelefone(data.telefone)          
        } catch (err) {
            console.error(err);
            // alert("An error occured while fetching user data");
        }
    };
 
    async function updateName() {      
        const docRef1 = doc(db, "users", user?.uid);
        await updateDoc(docRef1, {
            name: name
        });
    }

    async function updateCidade() {
        const docRef1 = doc(db, "users", user?.uid);
        await updateDoc(docRef1, {
            cidade: cidade
        });
    }

    async function updateDescricao() {
              const docRef1 = doc(db, "users", user?.uid);
        await updateDoc(docRef1, {
            descricao: descricao
        });
    }

    async function updateTelefone() {
               const docRef1 = doc(db, "users", user?.uid);
        await updateDoc(docRef1, {
            telefone: telefone
        });
    }

    async function updateWhatsapp() {
     
        const docRef1 = doc(db, "users", user?.uid);
        await updateDoc(docRef1, {
            whatsapp: whatsapp
        });
    }

    async function updatePhotoEmail() {
        const docRef1 = doc(db, "users", user?.uid);
        await updateDoc(docRef1, {
            photo: user?.photoURL,
            email: user?.email
        });
    }

  
    useEffect(() => {
        if (loading) return;
        if (!user) return navigate("/");
        fetchUserName();
        updatePhotoEmail();
    }, [user, loading]);

    return (
        <div>
            <Header />
          
                <div className="App">
                    <div className="dashboard__container">
                        <br />
                        <h1 style={{ color: 'white' }}>Meu perfil</h1>
                        <div>
                            <Link to="/">Voltar ao inicio</Link>
                        </div>
                        <br />
                        <img src={`${user?.photoURL}`} width="175" height="175" style={{ borderRadius: '50%', alignSelf: 'center' }} />
                        <br />
                        <div style={{ flexDirection: 'row', display: 'flex', paddingLeft: '20px', }}>
                            <h2 style={{ color: 'white', paddingRight:'20px' }}>{name ? name : 'Informe o seu nome'}</h2>
                            <MDBBtn size='lg' floating outline onClick={toggleShowName}><EditIcon color="success" /></MDBBtn>
                            <MDBModal show={nameModal} setShow={setNameModal} tabIndex='-1'>
                                <MDBModalDialog>
                                    <MDBModalContent>
                                        <MDBModalHeader>
                                            <MDBModalTitle>Informe ou altere o seu nome</MDBModalTitle>
                                            <MDBBtn className='btn-close' color='none' onClick={toggleShowName}></MDBBtn>
                                        </MDBModalHeader>
                                        <MDBModalBody><input placeholder="Insira seu nome" type='text' name='name' onChange={(e) => setName(e.target.value)} style={{ borderRadius: '10px', height: '40px', width: '400px' }} /></MDBModalBody>
                                        <MDBModalFooter>
                                            <MDBBtn color='secondary' onClick={toggleShowName}>
                                            Voltar
                                            </MDBBtn>
                                            <MDBBtn onClick={updateName}>Salvar alterações</MDBBtn>
                                        </MDBModalFooter>
                                    </MDBModalContent>
                                </MDBModalDialog>
                            </MDBModal>
                        </div>
                        <br />
                        <div style={{ flexDirection: 'row', display: 'flex', paddingLeft: '20px' }}>
                            <h2 style={{ color: 'white', paddingRight:'20px' }}>{cidade ? cidade : 'Informe sua cidade'}</h2>
                            <MDBBtn size='lg' floating outline onClick={toggleShowCidade}><EditIcon color="success" /></MDBBtn>
                            <MDBModal show={cidadeModal} setShow={setCidadeModal} tabIndex='-1'>
                                <MDBModalDialog>
                                    <MDBModalContent>
                                        <MDBModalHeader>
                                            <MDBModalTitle>Informe ou altere a sua cidade</MDBModalTitle>
                                            <MDBBtn className='btn-close' color='none' onClick={toggleShowCidade}></MDBBtn>
                                        </MDBModalHeader>
                                        <MDBModalBody> <input placeholder="Insira sua cidade" type='text' name='cidade' onChange={(e) => setCidade(e.target.value)} style={{ borderRadius: '10px', height: '40px', width: '400px' }} /></MDBModalBody>
                                        <MDBModalFooter>
                                            <MDBBtn color='secondary' onClick={toggleShowCidade}>
                                            Voltar
                                            </MDBBtn>
                                            <MDBBtn onClick={updateCidade}>Salvar alterações</MDBBtn>
                                        </MDBModalFooter>
                                    </MDBModalContent>
                                </MDBModalDialog>
                            </MDBModal>
                        </div>
                        <br />
                        <div style={{flexDirection: 'row', display:'flex', paddingLeft:'20px'}}>
                        <h2 style={{ color: 'white', paddingRight:'20px' }}>{descricao ? descricao : 'Informe sua descrição'}</h2>
                        <MDBBtn size='lg' floating outline onClick={toggleShowDescricao}><EditIcon color="success" /></MDBBtn>
                        <MDBModal show={descricaoModal} setShow={setDescricaoModal} tabIndex='-1'>
                            <MDBModalDialog>
                                <MDBModalContent>
                                    <MDBModalHeader>
                                        <MDBModalTitle>Informe ou altere sua descrição</MDBModalTitle>
                                        <MDBBtn className='btn-close' color='none' onClick={toggleShowDescricao}></MDBBtn>
                                    </MDBModalHeader>
                                    <MDBModalBody> <textarea placeholder="Insira sua descricao" type='text' name='descricao' onChange={(e) => setDescricao(e.target.value)} style={{ borderRadius: '10px', height: '40px', width: '400px' }} /></MDBModalBody>
                                    <MDBModalFooter>
                                        <MDBBtn color='secondary' onClick={toggleShowDescricao}>
                                        Voltar
                                        </MDBBtn>
                                        <MDBBtn onClick={updateDescricao}>Salvar alterações</MDBBtn>
                                    </MDBModalFooter>
                                </MDBModalContent>
                            </MDBModalDialog>
                        </MDBModal>
                        </div>
                        <br />
                        <div style={{ flexDirection: 'row', display: 'flex', paddingLeft: '20px' }}>
                            <h2 style={{ color: 'white', paddingRight:'20px' }}>{whatsapp ? whatsapp : 'Informe seu whatsapp'}</h2>
                            <MDBBtn size='lg' floating outline onClick={toggleShowWhatsapp}><EditIcon color="success" /></MDBBtn>
                            <MDBModal show={whatsappModal} setShow={setWhatsappModal} tabIndex='-1'>
                                <MDBModalDialog>
                                    <MDBModalContent>
                                        <MDBModalHeader>
                                            <MDBModalTitle>Informe ou altere o seu whatsapp</MDBModalTitle>
                                            <MDBBtn className='btn-close' color='none' onClick={toggleShowWhatsapp}></MDBBtn>
                                        </MDBModalHeader>
                                        <MDBModalBody> <input placeholder="Insira seu whatsapp" type='text' name='whatsapp' onChange={(e) => setWhatsapp(e.target.value)} style={{ borderRadius: '10px', height: '40px', width: '400px' }} /></MDBModalBody>
                                        <MDBModalFooter>
                                            <MDBBtn color='secondary' onClick={toggleShowWhatsapp}>
                                            Voltar
                                            </MDBBtn>
                                            <MDBBtn onClick={updateWhatsapp}>Salvar alterações</MDBBtn>
                                        </MDBModalFooter>
                                    </MDBModalContent>
                                </MDBModalDialog>
                            </MDBModal>
                        </div>
                        <br />
                        <div style={{flexDirection: 'row', display:'flex', paddingLeft:'20px'}}>
                        <h2 style={{ color: 'white', paddingRight:'20px' }}>{telefone ? telefone : 'Informe seu telefone'}</h2>
                        <MDBBtn size='lg' floating outline onClick={toggleShowTelefone}><EditIcon color="success" /></MDBBtn>
                        <MDBModal show={telefoneModal} setShow={setTelefoneModal} tabIndex='-1'>
                            <MDBModalDialog>
                                <MDBModalContent>
                                    <MDBModalHeader>
                                        <MDBModalTitle>Informe ou altere o seu telefone</MDBModalTitle>
                                        <MDBBtn className='btn-close' color='none' onClick={toggleShowTelefone}></MDBBtn>
                                    </MDBModalHeader>
                                    <MDBModalBody> <input placeholder="Insira seu telefone" type='text' name='telefone' onChange={(e) => setTelefone(e.target.value)} style={{ borderRadius: '10px', height: '40px', width: '400px' }} /></MDBModalBody>
                                    <MDBModalFooter>
                                        <MDBBtn color='secondary' onClick={toggleShowTelefone}>
                                        Voltar
                                        </MDBBtn>
                                        <MDBBtn onClick={updateTelefone}>Salvar alterações</MDBBtn>
                                    </MDBModalFooter>
                                </MDBModalContent>
                            </MDBModalDialog>
                        </MDBModal>
                        </div>
                             <br />
                        <h4 style={{ color: 'white' }}>{user?.email}</h4>
                        <br />                       
                        <button className="dashboard__btn" onClick={logout}>
                            Sair
                        </button>
                    </div>
                </div>
                  </div>
    );
}


export default ProfileEdit;
